    font-family: Poppins-Regular;
    font-size: 15px;
    color: #fff;
    line-height: 1.2;
    padding: 0 5px;
}
/*//////////////////////////////////////////////////////////////////
LINE FOCUS*/
/*---------------------------------------------
Focus of Input*/
.inputFocus {
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    pointer-events: none;
}
/*---------------------------------------------
Focus Line*/
.inputFocus::before {
    content: "";
    display: block;
    position: absolute;
    bottom: -2px;
    left: 0;
    width: 0;
    height: 2px;
    -webkit-transition: all 0.4s;
    transition: all 0.4s;
    background: #fff;
}
/*---------------------------------------------
Focus when line is being typed (Temporal focus)*/
.inputLine:focus + .inputFocus::before {
    width: 100%;
}
/*---------------------------------------------
Focus when line has text (Permanent focus)*/
.has-val.inputLine + .inputFocus::before {
    width: 100%;
}
/*//////////////////////////////////////////////////////////////////
VALIDATION*/
/*---------------------------------------------
Text box of error*/
.alertValidation::before {
    content: attr(data-validate);
    position: absolute;
    max-width: 70%;
    background-color: #fff;
    border: 1px solid #c80000;
    border-radius: 2px;
    padding: 4px 25px 4px 10px;
    top: 50%;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
    right: 0px;
    pointer-events: none;
    font-family: Poppins-Regular;
    color: #c80000;
    font-size: 13px;
    line-height: 1.4;
    text-align: left;
    visibility: hidden;
    opacity: 0;
    -webkit-transition: opacity 0.4s;
    transition: opacity 0.4s;
}
/*---------------------------------------------
Exclamation mark of error*/
.alertValidation::after {
    content: "\F12A";
    font-family: FontAwesome;
    display: block;
    position: absolute;
    color: #c80000;
    font-size: 16px;
    top: 50%;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
    right: 8px;
}
/*---------------------------------------------
Show text box on hover*/
.alertValidation:hover::before {
    visibility: visible;
    opacity: 1;
}
/*---------------------------------------------
Show text box always if screen is smaller (Possible mobile and can't hover)*/
@media (max-width: 992px) {
.alertValidation::before {
        visibility: visible;
        opacity: 1;
}
